import cartMainList from './cart-main-list'
import cartMainRecommend from './cart-main-recommend'
export default {
  props: ['list'],
  components: {
    cartMainList,
    cartMainRecommend
  },
  methods: {
    goodsNumber (o) {
      this.$emit('goods-number', o)
    },
    select (o) {
      this.$emit('select', o)
    },
    selectGroup (o) {
      this.$emit('select-group', o)
    }
  }
}



