import cartMainListGroup from './cart-main-list-group'

export default {
  props: ['list'],
  components: {
    cartMainListGroup
  },
  methods: {
    goodsNumber (o) {
      this.$emit('goods-number', o)
    },
    select (o) {
      this.$emit('select', o)
    },
    selectGroup (o) {
      this.$emit('select-group', o)
    }
  }
}



