export default {
  props: ['cartInfo'],
  methods: {
    selectAll () {
      this.$emit('select-all')
    },
    deleteGoods () {
      this.$emit('delete-goods', this.cartInfo.select)
    },
    checkout () {
      this.$emit('checkout', this.cartInfo.select)
    }
  }
}



